<template>
  <div class="home">
    <div class="banner">
      <van-swipe :autoplay="3000" height="250">
        <van-swipe-item v-for="(item, index) in bannerData" :key="index" @click="proDetails(item.pid)">
          <div class="banner-img">
          <img class="auto-img" v-lazy="item.bannerImg" />
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="home-content">
      <h1>
        Hello,
        <span v-if="isLogin">{{userInfo.nickName}}</span>
        <span v-else>陌生人</span>
      </h1>
      <h2>咖啡会让你脑洞大开哟</h2>
      <div class="btns clearfix">
        <div class="btn btn-left">
          <img src="../../assets/img/btnLeft.png" alt />
        </div>
        <div class="btn btn-right clearfix">
          <img src="../../assets/img/btnRight.png" alt />
        </div>
      </div>
      <div class="hotRecom">
        <h2>热门推荐</h2>
        <div class="tab-pro fl" v-for="(item,index) in hotRecomData" :key="index" @click="proDetails(item.pid)">
              <img class="auto-img" :src="item.largeImg" alt />
              <h4>{{item.name }} 
                <span>
                  ￥{{item.price}}
                </span>
              </h4>
            </div>
        <!-- <van-tabs
          swipeable
          animated
          line-height="2px"
          color="#0b34ba"
          v-model="evdayRecomData.active"
        >
          <van-tab
            :title-style="index==evdayRecomData.active?{color:'#0b34ba'}:''"
            v-for="(item,index) in evdayRecomData.listData"
            :title="item.week"
            :key="index"
          >
            <div class="tab-pro fl" v-for="(i,ind) in item.content"
            :key="ind">
              <img class="auto-img" :src="i.img" alt />
              <h4>{{i.proName }}</h4>
            </div>
          </van-tab>
        </van-tabs>-->
      </div>
    </div>
  </div>
</template>

<script>
import "../../assets/css/home.less";
export default {
  name: "Home",
  data() {
    return {
      isLogin:false,
      date: {
        week: 0,
      },
      bannerData: [],
      hotRecomData: [],
      userInfo:{},
    };
  },
  created() {
    this.getBannerData();
    this.getHotRecomData();
    this.getDate();
    this.getUserInfo()
  },
  methods: {
    getBannerData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 300) {
            this.bannerData = result.data.result;
          }
          //  
        })
        .catch((err) => {
           
        });
    },
    getHotRecomData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 500) {
          this.hotRecomData = result.data.result;
          }
          //  
        })
        .catch((err) => {
           
        });
    },
    getDate() {
      this.date.week = new Date().getDay() - 1;
      //  
      // this.evdayRecomData.active = this.date.week
    },
    // 商品点击事件
    proDetails(pid){
      this.$router.push({name:"Details",query:{pid}})
    },
    // 获取用户信息
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
           
          this.$toast.clear();
          if (result.data.code == "A001") {
            if (result.data.result.length > 0) {
              this.isLogin = true;
              this.userInfo = result.data.result[0];
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

  },
};
</script>

<style lang="less" scoped>
</style>